.container {
  max-width: $mobile;
  margin: 0 auto;
  padding-left: 20px;
  padding-right: 20px;

  @media screen and (min-width: $tablet) {
    max-width: $tablet;
    padding-left: 75px;
    padding-right: 75px;
  }
  @media screen and (min-width: $decktop) {
    max-width: $decktop;
    padding-left: 71px;
    padding-right: 71px;
  }
}
